.hero-img{
    width: 100%;
    height: 60vh;
    background: rgba(0, 0, 0, 0.5);
    position: relative;
}
.hero-img::after{
    content: '';
    background: url('../assets/hero2.jpg');
    background-position:center top;
    background-size: cover;
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
}
.heading{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.hero-img h1{
    font-size: 2.4rem;
}
.hero-img p{
    font-size: 1.4rem;
    text-align: center;
}
@media screen and (max-width:640px) {
    .hero-img h1{
        font-size: 2rem;
    }
}